import { Grid } from '@components/containers/Grid'

import { ItemCircle } from './ItemCircle'
import s from './style.module.scss'

export const Admission = () => {
    const year = new Date().getFullYear()

    return (
        <Grid
            id="Admission"
            className={`Admission ${s.Admission}`}
        >
            <h2> {year}</h2>
            <ItemCircle
                text1="40"
                text2={
                    <>
                        
                        <br />
                         
                    </>
                }
                text3={<> {year} </>}
            />

            <ItemCircle
                text1="118"
                text2={' '}
                text3={<> {year} </>}
            />

            <ItemCircle
                text1="4"
                text2={'  '}
                text3="+      
                ,   !"
            />
        </Grid>
    )
}
